const shared = {
    id: 0,
    x: 0,
    y: 0,
    safeId(id = 0) {
        return Math.floor(id < 1 ? 1 : id > 10201 ? 10201 : id);
    },
    update(id = 0) {
        let vid = this.safeId(id);
        let c = this.getCoordinates(vid);
        this.id = vid;
        this.x = c.x;
        this.y = c.y;
        runFunction("coordinates", "updateInputs");
        return vid;
    },
    getCoordinates(id = 0) {
        let w = Math.floor(id) - 1;
        let x = Math.floor(w / 101);
        let y = Math.floor(w - 101 * x);
        return { x: x - 50, y: y - 50 };
    },
    getId(x = 0, y = 0) {
        return (x + 50) * 101 + (y + 50 + 1);
    }
}
let updateProps = (name, props) => sharedMemory.safe(name, e => e.updateProps(props));
let runFunction = (name, func, param) => sharedMemory.safe(name, e => e[func](param));
let requestMap = id => runFunction("map-view", "request", shared.update(id));

class Map extends Pure {
    coordinate(c = 0) {
        return (c < -50 ? (c + 101) : (c > 50 ? (c - 101) : c));
    }
    navigate(delta = 0, v = true) {
        return e => {
            e.preventDefault();
            console.time("Navigate");
            if (v)
                requestMap(shared.getId(this.coordinate(shared.x + delta), shared.y));
            else
                requestMap(shared.getId(shared.x, this.coordinate(shared.y + delta)));
        }
    }
    onRemoved() {
        // Reset to solve map problem:
        shared.id = 0;
    }
    render() {
        if (!client.ready)
            return <></>;
        if (!client.authorized)
            return <Navigate to="/Home" />;
        if (shared.id === 0)
            shared.id = shared.update(parseInt(new URLSearchParams(window.location.search).get("id")) || client.activeVillageId || 1);
        return <div id="Map">
            <FieldInfo register="map-info" />
            <svg className="background" viewBox="0 0 540 316">
                <image xlinkHref={ImageMap.map.background} width={540} height={316} />
                <MapView register="map-view" />
                <Link to="#" onClick={this.navigate(-1)}><polygon points={Land.Arrows[0]} /></Link>
                <Link to="#" onClick={this.navigate(-1, false)}><polygon points={Land.Arrows[1]} /></Link>
                <Link to="#" onClick={this.navigate(1, false)}><polygon points={Land.Arrows[2]} /></Link>
                <Link to="#" onClick={this.navigate(1)}><polygon points={Land.Arrows[3]} /></Link>
            </svg>
            <Coordinates register="coordinates" />
            <svg class="direction" viewBox="0 0 100 57">
                <image xlinkHref={ImageMap.map.dir} width={100} height={57} />
                <Link to="#" onClick={this.navigate(7, false)}><polygon points="51,15 73,3 95,15 73,27" /></Link>
                <Link to="#" onClick={this.navigate(7)}><polygon points="51,41 73,29 95,41 73,53" /></Link>
                <Link to="#" onClick={this.navigate(-7, false)}><polygon points="4,41 26,29 48,41 26,53" /></Link>
                <Link to="#" onClick={this.navigate(-7)}><polygon points="4,15 26,3 48,15 26,27" /></Link>
            </svg>
        </div>;
    }
}

class Coordinates extends Pure {
    x = shared.x;
    y = shared.y;
    inputX = React.createRef();
    inputY = React.createRef();
    safe(c = 0) { return (c < -50 ? -50 : c > 50 ? 50 : c); }
    updateMap() {
        let x = this.x;
        let y = this.y;
        if (isNaN(x) || isNaN(y)) return;
        requestMap(shared.getId(this.safe(x), this.safe(y)));
    }
    updateInputs() {
        this.inputX.current.value = shared.x;
        this.inputY.current.value = shared.y;
    }
    number(input, fallback) {
        let n = Number(input.target.value);
        return this.safe(isNaN(n) ? fallback : n);
    }
    render() {
        return <form onSubmit={e => { e.preventDefault(); this.updateMap(); }} className="CoordinatesForm">
            <input
                type="number"
                min={-50}
                max={50}
                defaultValue={shared.x}
                ref={this.inputX}
                onBlur={e => (e.target.value = this.x)}
                onChange={e => (this.x = this.number(e, shared.x))}
            />
            <input
                type="number"
                min={-50}
                max={50}
                defaultValue={shared.y}
                ref={this.inputY}
                onBlur={e => (e.target.value = this.y)}
                onChange={e => (this.y = this.number(e, shared.y))}
            />
            <Button type="submit" size="sm" variant="dark">بحث</Button>
        </form>;
    }
}

class FieldInfo extends Pure {
    view = false;
    x = shared.x;
    y = shared.y;
    landType = 0;
    taken = 0;
    landName = "";
    playerName = "";
    race = 1;
    population = 0;
    allianceName = "";
    isVillage = 0;
    getLandFeilds() {
        return this.landName + " " + getVillageType(this.landType).join("-");
    }
    render() {
        return <>
            <h4>خريطة ({this.view ? `${this.x} , ${this.y}` : `${shared.x} , ${shared.y}`})</h4>
            <table style={{ opacity: (this.view && this.taken) ? 1 : 0.5 }} className="info_box table_collapsed">
                <tbody>
                    <tr><th colSpan={2}>{this.view ? (this.isVillage && !this.taken ? this.getLandFeilds() : this.landName) : "التفاصيل"}</th></tr>
                    <tr><td>اللاعب:</td><td>{this.view ? this.playerName : "-"}</td></tr>
                    <tr><td>السكان:</td><td>{(this.view && this.taken && this.isVillage) ? this.population : "-"}</td></tr>
                    <tr><td>التحالف:</td><td>{this.view ? this.allianceName : "-"}</td></tr>
                </tbody>
            </table>
        </>;
    }
}

class MapView extends Pure {
    state = { Content: [], id: 0 };
    view(info, data) {
        updateProps("map-info", {
            playerName: info[0],
            landName: info[1],
            allianceName: info[2],
            race: data[1],
            isVillage: data[2],
            taken: data[3],
            population: data[4],
            x: data[5],
            y: data[6],
            landType: data[7],
            view: true
        });
    }
    hide() { updateProps("map-info", { view: false }) }
    request(id = 1) {
        console.log(id);
        client.mapViewNormal(id).then(map => {
            if (!this.componentIsReady) return;
            let images = [];
            let links = [];
            let ruler = [];
            let rIncX = 0;
            let rIncY = 1;
            map.info.forEach((info, index) => {
                let data = map.data[index];
                let P = Land.Position[index];
                let C = Land.Polygon[index];
                images.push(<image key={`Map-Image-${index}-${data[0]}`} xlinkHref={ImageMap.map[info[3]]} x={P[0]} y={P[1]} width="74" height="74" />);
                links.push(<Link
                    onBlur={() => this.hide()}
                    onMouseLeave={() => this.hide()}
                    onFocus={() => this.view(info, data)}
                    onMouseEnter={() => this.view(info, data)}
                    key={`Map-Link-${index}-${data[0]}`}
                    to={"/View?id=" + data[0]}>
                    <polygon points={C} opacity={0} />
                </Link>);
                let R = Land.Ruler;
                if (index % 7 === 0) {
                    let Y = R[rIncY];
                    ruler.push(<text key={`Map-Ruler-Y-${index}-${data[0]}`} x={Y[0] + 30} y={Y[1] + 14}>{data[5]}</text>);
                    rIncY += 2;
                }
                if (index < 7) {
                    let X = R[rIncX];
                    ruler.push(<text key={`Map-Ruler-X-${index}-${data[0]}`} x={X[0] + 30} y={X[1] + 12}>{data[6]}</text>);
                    rIncX += 2;
                }
            });
            updateProps("map-info", { x: shared.x, y: shared.y });
            updateProps("coordinates", { x: shared.x, y: shared.y });
            this.updateState({ Content: images.concat(links, ruler) });
        });
    }
    onReady() {
        this.request(shared.id);
    }
    render() {
        return this.state.Content;
    }
}

